const createList = value => {
  return createNode(value); 
};
const appendList = (list, value) => {
  const node = createNode(value);
  let x = list;
  while(x.next){
    x = x.next;
  }
  x.next = node;
  return node;
};
const removeFromList = (list, node) => {
  let x = list;
  let p = node;
  while(x !== node && x !== null){
    p = x
    x = x.next
  }
  if(x === null){ // Ëã• x ‰∏∫ nullÔºåÂàô‰∏çÈúÄË¶ÅÂà†Èô§ÔºåÁõ¥Êé• returnÔºå false Ë°®Á§∫Êó†Ê≥ïÂà†Èô§‰∏çÂú®listÈáåÁöÑËäÇÁÇπ
    return false
  } else if (x === p){ // ËøôËØ¥ÊòéË¶ÅÂà†Èô§ÁöÑËäÇÁÇπÊòØÁ¨¨‰∏Ä‰∏™ËäÇÁÇπ
    p = x.next
    return p // Â¶ÇÊûúÂà†Èô§ÁöÑÊòØÁ¨¨‰∏Ä‰∏™ËäÇÁÇπÔºåÈÇ£‰πàÂ∞±Ë¶ÅÊääÊñ∞ list ÁöÑÂ§¥ËäÇÁÇπ p ËøîÂõûÁªôÂ§ñÈù¢ÔºåÂç≥ newList = removeFromList(list, list)
  } else {
    p.next = x.next;
    return list // Â¶ÇÊûúÂà†Èô§ÁöÑ‰∏çÊòØÁ¨¨‰∏Ä‰∏™ËäÇÁÇπÔºåËøîÂõûÂéüÊù•ÁöÑ list Âç≥ÂèØ
  }
};

const createNode = value => {
  return {
    data: value,
    next: null
  };
};

const travelList = (list, fn) => {
  let x = list
  while(x !==null){
    fn(x)
    x = x.next
  }
}

